import React from'react';export default class AllDocViewer extends React.Component{constructor(props){super(props);this.state={requestedUrl:props.url||"",requestedHeight:"",requestedwidth:""};} componentDidMount(){if(this.props){this.setState({requestedUrl:(this.props&&this.props.url)?this.props.url:"",requestedHeight:(this.props&&this.props.height)?this.props.height:window.innerHeight,requestedWidth:(this.props&&this.props.width)?this.props.width:"100%"})};} render(){const{requestedUrl,requestedHeight,requestedWidth}=this.state;return(React.createElement("iframe",{src:"https://docs.google.com/viewer?url="+requestedUrl+"&embedded=true",width:requestedWidth,height:requestedHeight,background:"#eee"}));}}